.btn--animation:focus .btn__item, .btn--animation:hover .btn__item {
  animation: none;
}

@keyframes turning {
  0% {
    border-radius: 30px;
    transform: rotate(0deg);
  }

  25% {
    border-radius: 30px 30px 30px 0;
    transform: rotate(90deg);
  }

  50% {
    border-radius: 30px 30px 0 0;
    transform: rotate(180deg);
  }

  75% {
    border-radius: 30px 0 0;
    transform: rotate(270deg);
  }

  100% {
    -webkit-box-reflect: below 5px linear-gradient(transparent, #fff);
    background-color: #b5ff00;
    border-radius: 0;
    transform: rotate(360deg);
    box-shadow: 0 0 5px #b5ff00, 0 0 25px #b5ff00, 0 0 50px #b5ff00, 0 0 100px #b5ff00;
  }
}

@keyframes skale3D {
  0% {
    -webkit-box-reflect: below 5px linear-gradient(transparent, #fff);
    transform: scale3d(1, 1, 1);
  }

  12% {
    -webkit-box-reflect: below 5px linear-gradient(transparent, #fff);
    transform: scale3d(1.15, 1.15, 1.15);
  }

  25% {
    -webkit-box-reflect: below 5px linear-gradient(transparent, #fff);
    transform: scale3d(1.25, 1.25, 1.25);
  }

  37% {
    -webkit-box-reflect: below 5px linear-gradient(transparent, #fff);
    transform: scale3d(1.37, 1.37, 1.37);
  }

  50% {
    -webkit-box-reflect: below 5px linear-gradient(transparent, #fff);
    transform: scale3d(1.5, 1.5, 1.5);
  }

  62% {
    -webkit-box-reflect: below 5px linear-gradient(transparent, #fff);
    transform: scale3d(1.37, 1.37, 1.37);
  }

  75% {
    -webkit-box-reflect: below 5px linear-gradient(transparent, #fff);
    transform: scale3d(1.25, 1.25, 1.25);
  }

  87% {
    -webkit-box-reflect: below 5px linear-gradient(transparent, #fff);
    transform: scale3d(1.15, 1.15, 1.15);
  }

  100% {
    -webkit-box-reflect: below 5px linear-gradient(transparent, #fff);
    transform: scale3d(1, 1, 1);
  }
}

@keyframes heartBeat {
  0% {
    transform: translate(-50%, -50%);
  }

  20% {
    transform: translate(-50%, -50%)scale(1.15);
  }

  40% {
    transform: translate(-50%, -50%)scale(.9);
  }

  60% {
    transform: translate(-50%, -50%)scale(1.1);
  }

  80% {
    transform: translate(-50%, -50%)scale(1);
  }

  100% {
    transform: translate(-50%, -50%)scale(1);
  }
}

@keyframes btn-anim1 {
  0% {
    left: -100%;
  }

  50%, 100% {
    left: 100%;
  }
}

@keyframes btn-anim2 {
  0% {
    top: -100%;
  }

  50%, 100% {
    top: 100%;
  }
}

@keyframes btn-anim3 {
  0% {
    right: -100%;
  }

  50%, 100% {
    right: 100%;
  }
}

@keyframes btn-anim4 {
  0% {
    bottom: -100%;
  }

  50%, 100% {
    bottom: 100%;
  }
}

@keyframes slider {
  0% {
    transform: scale3d(1, 1, 1);
  }

  5% {
    transform: scale3d(2, 2, 2);
  }

  25% {
    transform: scale3d(1.75, 1.75, 1.75);
  }

  50% {
    transform: scale3d(1.5, 1.5, 1.5);
  }

  75% {
    transform: scale3d(1.25, 1.25, 1.25);
  }

  100% {
    transform: scale3d(1, 1, 1);
  }
}

.backdrop {
  width: 100%;
  height: 100%;
  z-index: 1000;
  opacity: 1;
  background-color: rgba(0, 0, 0, .2);
  transition: opacity .4s ease-in-out, visibility .4s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity .4s ease-in-out;
}

.modal {
  width: 100%;
  max-width: 320px;
  max-height: 449px;
  z-index: 30;
  background-image: radial-gradient(circle, #fff, #ffe550);
  border-radius: 30px;
  padding: 32px 20px;
  transition: transform .4s ease-in-out;
  animation: heartBeat 2s ease-in-out .2s both;
  position: absolute;
  top: 250px;
  left: 50%;
  transform: translate(-50%);
  box-shadow: 0 4px 24px rgba(105, 105, 105, .1);
}

@media screen and (min-width: 480px) and (min-height: 449px) {
  .modal {
    max-width: 458px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

.backdrop.is-hidden .modal {
  opacity: 0;
  animation-play-state: paused;
  transform: translate(50%)scale(1.1);
}

.modal-btn {
  width: 32px;
  height: 32px;
